import "./styles.css";
import card from "./card";

function menus(index) {
  const items = [];
  const keys = Object.keys(card[index]);
  const values = Object.values(card[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <>
      {menus(0)}
      {menus(1)}
      {menus(2)}
    </>
  );
}
